@import "variables";

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



h1, h2, h3, h4 {
  font-family: 'DM Serif Text', serif;
}

h1 {
  display: block;
  font-size: 2.285em;
  margin-top: 0.87em;
  margin-bottom: 0.97em;
  margin-left: 0;
  margin-right: 0;
  font-weight: 400;
  letter-spacing: -0.6px;
  @media screen and (min-width: 600px) {
    font-size: 2.538em;
  }
  @media screen and (min-width: 1200px) {
    font-size: 2.734em;
  }
  @media screen and (min-width: 1600px) {
    font-size: 2.870em;
  }
}
h2 {
  display: block;
  font-size: 1.828em;
  margin-top: 1.33em;
  margin-bottom: 0.46em;
  margin-left: 0;
  margin-right: 0;
  font-weight: 400;
  letter-spacing: -0.6px;
  @media screen and (min-width: 600px) {
    font-size: 2.031em;
  }
  @media screen and (min-width: 1200px) {
    font-size: 2.188em;
  }
  @media screen and (min-width: 1600px) {
    font-size: 2.296em;
  }
}
h3 {
  display: block;
  font-size: 1.4625em;
  margin-top: 1.3em;
  margin-bottom: 0.7em;
  margin-left: 0;
  margin-right: 0;
  font-weight: 400;
  letter-spacing: -0.6px;
  @media screen and (min-width: 600px) {
    font-size: 1.625em;
  }
  @media screen and (min-width: 1200px) {
    font-size: 1.750em;
  }
  @media screen and (min-width: 1600px) {
    font-size: 1.837em;
  }
}
h4 {
  display: block;
  font-size: 1.17em;
  margin-top: 1.33em;
  margin-bottom: 0.73em;
  margin-left: 0;
  margin-right: 0;
  font-weight: 400;
  @media screen and (min-width: 600px) {
    font-size: 1.3em;
  }
  @media screen and (min-width: 1200px) {
    font-size: 1.4em;
  }
  @media screen and (min-width: 1600px) {
    font-size: 1.47em;
  }
}


ul, ol {
  li {
    font-size: 1.1em;
    line-height: 1.5em;
    @media screen and (min-width: 600px) {
      font-size: 1.2em;
    }
    @media screen and (min-width: 1200px) {
      font-size: 1.3em;
    }
    @media screen and (min-width: 1600px) {
      font-size: 1.4em;
    }
  }
  &.list, &.ordered-list {
    margin: 0 0 .8em 2.6em;
  }
  &.plain {
    margin: 0 0 .8em 0;
  }
  &.list {
    li {
      list-style-type: disc;
    }
  }
  &.ordered-list {
    li {
      list-style-type: decimal;
    }
  }
}



p, li {
  font-family: 'Playfair Display', serif;
  line-height: 1.5em;
  font-size: 1.1em;
  color: $transparent-heavy;
  @media screen and (prefers-color-scheme: dark) {
    color: $dm-transparent-heavy;
  }
  @media screen and (min-width: 600px) {
    font-size: 1.2em;
  }
  @media screen and (min-width: 1200px) {
    font-size: 1.3em;
  }
  @media screen and (min-width: 1600px) {
    font-size: 1.4em;
  }
}

p {
  margin-bottom: 0.8em;
}

a {
  color: $transparent-heavy;
  @media screen and (prefers-color-scheme: dark) {
    color: $dm-transparent-heavy;
  }
}

i, figcaption {
  font-style: italic;
}

.intro {
  font-size: 1.2em;
  @media screen and (min-width: 600px) {
    font-size: 1.3em;
  }
  @media screen and (min-width: 1200px) {
    font-size: 1.4em;
  }
  @media screen and (min-width: 1600px) {
    font-size: 1.5em;
  }
}

.dropcaps {
  &:first-letter {
    float: left;
    font-size: 3.23em;
    line-height: .82em;
    padding: 0px 8px 0 0;
  }
}
@-moz-document url-prefix() {
  .dropcaps {
    &:first-letter {
      padding: 9px 8px 0 0;
    }
  }
}

figcaption {
  font-family: 'Playfair Display', serif;
  line-height: 1.5em;
  font-size: .9em;
  color: $transparent-medium;
  @media screen and (prefers-color-scheme: dark) {
    color: $dm-transparent-medium;
  }
  margin-bottom: 0.8em;
  @media screen and (min-width: 600px) {
    font-size: .95em;
  }
  @media screen and (min-width: 1200px) {
    font-size: 1.0em;
  }
  @media screen and (min-width: 1600px) {
    font-size: 1.1em;
  }
}

.metadata {
  ul {
    li {
      font-size: 1em;
    }
  }
}

.intermezzo {
  p {
    font-size: 1em;
  }
  @media screen and (min-width: 1600px) {
    p {
      font-size: 1em;
    }
  }
}
