@import "variables";

html, body {
  background: linear-gradient(90deg, #11202b, #263b46, #3c5863, #557780, #70989d, #8db9ba, #acdcd6, #cffff2);
  @media screen and (prefers-color-scheme: dark) {
    background: linear-gradient(90deg, #0b151c, #18252c, #25363c, #33474c, #425a5d, #526d6d, #64807c, #78948c);
  }
}

h1, h2, h3, h4 {
  color: $black;
  @media screen and (prefers-color-scheme: dark) {
    color: $dm-transparent-heavy;
  }
}

header {
  position: relative;
  margin: 33px auto 56px;
  max-width: 80%;
  .logo {
    display: inline-block;
    vertical-align: bottom;
  }
  .logo, .franklabs a {
    text-decoration: none;
  }
  .franklabs {
    font-size: 2.1em;
    &:hover {
      text-decoration: underline;
    }
  }

  @media screen and (min-width: 600px) {
    max-width: 456px;
    margin: 44px auto 68px;
    .franklabs {
      font-size: 2.5em;
    }
  }
  @media screen and (min-width: 1200px) {
    max-width: 550px;
    // margin: 67px auto 108px;
    .franklabs {
      font-size: 2.7em;
    }
  }
  @media screen and (min-width: 1600px) {
    max-width: 570px;
    margin: 67px auto 108px;
    .franklabs {
      font-size: 2.9em;
    }
  }
  img {
    width: 100%;
    display: inline-block;
    vertical-align: bottom;
    @media screen and (min-width: 600px) {
      width: 170px;
      margin-bottom: -4px;
    }
    @media screen and (min-width: 1200px) {
      width: 245px;
      margin-bottom: -7px;
    }
    @media screen and (min-width: 1600px) {
      width: 250px;
      margin-bottom: -3px;
    }
  }
  .franklabs {
    display: inline-block;
    margin-left: 0.4em;
  }
  i {
    color: $theme-color-base;
  }
  .menu {
    a {
      color: $transparent-heavy;
      @media screen and (prefers-color-scheme: dark) {
        color: $dm-transparent-heavy;
      }
    }
    margin: 30px 0px 0px 0px;
    ul {
      li {
        display: inline-block;
        margin-right: .6em;
      }
    }
    @media screen and (min-width: 600px) {
      position: absolute;
      right: -11px;
      bottom: 1px;
    }
    @media screen and (min-width: 1200px) {
      position: absolute;
      right: -11px;
      bottom: 1px;
    }
    @media screen and (min-width: 1600px) {
      position: absolute;
      right: -11px;
      bottom: 5px;
    }
  }
}



.content-wrapper {
  position: relative;
  background-color: $theme-color-base;
  box-shadow:
    0 1.1px 1.2px -2px rgba(0, 0, 0, 0.072),
    0 2.5px 2.7px -2px rgba(0, 0, 0, 0.105),
    0 4.5px 4.8px -2px rgba(0, 0, 0, 0.129),
    0 7.4px 8px -2px rgba(0, 0, 0, 0.151),
    0 12.2px 13.2px -2px rgba(0, 0, 0, 0.175),
    0 21.3px 23.1px -2px rgba(0, 0, 0, 0.208),
    0 46px 50px -2px rgba(0, 0, 0, 0.28)
  ;
  @media screen and (prefers-color-scheme: dark) {
    background-color: $dm-theme-color-base;
  }
  margin: 5% auto 20em;
  max-width: 80%;
  padding: 5%;
  @media screen and (min-width: 600px) {
    max-width: 456px;
    margin: 2em auto 20em;
    padding: 2em;
  }
  @media screen and (min-width: 1200px) {
    max-width: 550px;
    margin: 3em auto 20em;
    padding: 3em;
  }
  @media screen and (min-width: 1600px) {
    max-width: 570px;
    margin: 3.5 auto 20em;
    padding: 3.5em;
  }
  h1:first-of-type {
    margin: 0 0 0.6em;
  }




  .metadata {
    position: relative;
    ul {
      margin-bottom: 2em;
      li {
        line-height: 1.4em;
        font-style: italic;
        color: $transparent-medium;
        @media screen and (prefers-color-scheme: dark) {
          color: $dm-transparent-heavy;
        }
        margin-left: 49px;
      }
    }
    .avatar {
      float: right;
      position: absolute;
      top: -2px;
      left: 0px;
      width: 36px;
      @media screen and (min-width: 1200px) {
        float: none;
        position: relative;
        top: 0px;
        width: 104px;
        margin-top: 10px;
      }
    }
    @media screen and (min-width: 1200px) {
      position: absolute;
      right: -267px;
      width: 250px;
      ul {
        li {
          line-height: 1.5em;
          margin-left: 0;
        }
      }
    }
  }

  .spacer-ava-gr1200 {
    @media screen and (min-width: 1200px) {
      height: 60px;
      width: 100%;
    }
  }

  figure {
    img {
      width: 100%;
    }
    figcaption {
      width: 100%;
      margin: 1em 0;
      text-align: center;
    }
    width: 120%;
    margin: 1.1em 0px 1.2em -10%;
    @media screen and (min-width: 600px) {
      width: 128%;
      margin: 1.5em 0px 1.1em -14%;
    }
    @media screen and (min-width: 1200px) {
      width: 140%;
      margin: 2em 0px 1.8em -20%;
    }
    @media screen and (min-width: 1600px) {
      width: 152%;
      margin: 2.6em 0px 2.4em -26%;
    }
  }


  .intro {
    border-bottom: 1px solid $theme-color-accent-light;
    @media screen and (prefers-color-scheme: dark) {
      border-bottom: 1px solid $dm-theme-color-accent-light;
    }
    padding-bottom: 1em;
  }

  .intermezzo {
    border: 1px solid black;
    @media screen and (prefers-color-scheme: dark) {
      border: 1px solid $dm-black;
    }
    padding: 1em 5% 0.4em;
    margin: 1.3em 0 1.1em;
    @media screen and (min-width: 600px) {
      border: 1px solid black;
      @media screen and (prefers-color-scheme: dark) {
        border: 1px solid $dm-black;
      }
    }
    @media screen and (min-width: 1200px) {
      border: 1px solid black;
      @media screen and (prefers-color-scheme: dark) {
        border: 1px solid $dm-black;
      }
      margin: 1.4em 0 1.2em;
    }
    @media screen and (min-width: 1600px) {
      position: absolute;
      right: -379px;
      width: 250px;
      margin-top: 0px;
      padding: 1.4em 5% 1.0em;
    }
    h4 {
      margin-top: 0px;
    }
  }

  .button {
    background-color: $transparent-light;
    @media screen and (prefers-color-scheme: dark) {
      background-color: $dm-transparent-light;
    }
    border: 1px solid rgba(0, 0, 0, 0);
    color: $white;
    @media screen and (prefers-color-scheme: dark) {
      color: $white;
    }
    padding: 1px 13px 4px;
    text-decoration: none;
    -webkit-font-smoothing: antialiased;
    border-radius: 22px;
    &:hover {
      border: 1px solid $black;
      @media screen and (prefers-color-scheme: dark) {
        border: 1px solid $dm-black;
      }
      background-color: transparent;
      color: $black;
      @media screen and (prefers-color-scheme: dark) {
        color: $dm-transparent-heavy;
      }
      cursor: pointer;
    }
    &.inverse {
      background-color: $white;
      @media screen and (prefers-color-scheme: dark) {
        background-color: $dm-white;
      }
      border: 1px solid $black;
      @media screen and (prefers-color-scheme: dark) {
        border: 1px solid $dm-black;
      }
      color: $black;
      @media screen and (prefers-color-scheme: dark) {
        color: $dm-black;
      }
      &:hover {
        background-color: $transparent-light;
        @media screen and (prefers-color-scheme: dark) {
          background-color: $dm-transparent-light;
        }
        border: 1px solid rgba(0, 0, 0, 0);
        color: $white;
        @media screen and (prefers-color-scheme: dark) {
          color: $dm-white;
        }
      }
    }
  }

}
